export default function queryLang ():  string | '' {
  let href = window.location.href.replace(/#/g, '');
  let search = href.slice(href.indexOf('?') + 1, href.length);
  if (!search || search === '0') {
    return ''
  }
  let dataArr = decodeURIComponent(search).split('&')
  let lang = ''
  dataArr.forEach(query => {
    let queryItem = query.split('=')
    console.log(queryItem)
    if (queryItem.length > 1 && queryItem[0] === 'lang') {
      lang = queryItem[1]
    }
  })
  return lang
};